.cart-dropdown {
	position: absolute;
	width: 240px;
	height: 340px;
	display: flex;
	flex-direction: column;
	padding: 20px;
	border: 1px solid black;
	background-color: white;
	top: 90px;
	right: 40px;
	z-index: 5;
	overflow-y: scroll;
	scrollbar-width: none;
	&::-webkit-scrollbar {
		display: none;
	}
	.empty-message {
		font-size: 20px;
		margin: 50px auto;
	}
	.cart-items {
		overflow: hidden;
		height: 240px;
		display: flex;
		flex-direction: column;
		overflow-y: scroll;
		scrollbar-width: none;
		&::-webkit-scrollbar {
			display: none;
		}
	}

	button {
		margin-top: auto;
	}
}
